import { Metadata } from 'next';
import LandingHeader from '@/components/landing/LandingHeader';
import HeroSection from '@/components/landing/HeroSection';
import FeaturesSection from '@/components/landing/FeaturesSection';
import GatewaysSection from '@/components/landing/GatewaysSection';
import BenefitsSection from '@/components/landing/BenefitsSection';
import HowItWorksSection from '@/components/landing/HowItWorksSection';
import TrialSection from '@/components/landing/TrialSection';
import CTASection from '@/components/landing/CTASection';
import LandingFooter from '@/components/landing/LandingFooter';
import { LegalModalProvider } from '@/components/legal/LegalModalProvider';
import { getAppName, shouldUseLandingPage } from '@/config/dynamicConfig';
import ClientRedirect from '@/components/common/ClientRedirect';

export async function generateMetadata(): Promise<Metadata> {
  const appName = await getAppName();
  
  return {
    title: `${appName} - Automatize suas cobranças e receba mais rápido`,
    description: `Gestão completa de cobranças recorrentes com envio automático via WhatsApp e baixa automática após pagamento com o ${appName}. Teste grátis por 7 dias.`,
    keywords: 'gestão de cobranças, cobrança automática, whatsapp, pagamentos, PIX, boleto, gateway de pagamento',
  };
}

export default async function LandingPage() {
  const useLandingPage = await shouldUseLandingPage();
  
  // Se landing page está desabilitada, mostrar componente de redirecionamento
  if (!useLandingPage) {
    return <ClientRedirect />;
  }

  return (
    <LegalModalProvider>
      <div className="min-h-screen bg-white dark:bg-gray-900">
        <LandingHeader />
        <main>
          <HeroSection />
          <FeaturesSection />
          <GatewaysSection />
          <BenefitsSection />
          <HowItWorksSection />
          <TrialSection />
          <CTASection />
        </main>
        <LandingFooter />
      </div>
    </LegalModalProvider>
  );
}

